import styled from 'styled-components'
import smallFurn1 from '../img/smallFurn1.jpg'
import smallFurn2 from '../img/smallFurn2.jpg'
import smallFurn3 from '../img/smallFurn3.jpg'
import smallFurn4 from '../img/smallFurn4.jpg'
import smallFurn5 from '../img/smallFurn5.jpg'
import smallFurn6 from '../img/smallFurn6.jpg'
// swiper react components
import SwiperCore, { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react'


// import swiper styles
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y]);

const FeaturedItems = () => {
   const pictures = [smallFurn1, smallFurn2, smallFurn3, smallFurn4, smallFurn5, smallFurn6]
    return(
        <Title>
            <h2>Featured Items</h2>
        
        <Box>
                <Swiper 
                spaceBetween={50}
                slidesPerView={4}
                navigation
                pagination={{ clickable: true }}
                // scrollbar={{ draggable: false }}
                onSwiper={(swiper) => console.log(swiper)}
                onSlideChange={() => console.log('slide change')}
                >
                    {pictures.map(pict => (
                        <SwiperSlide>
                                <img className='image' style={{ width: '80%', height: '100%'}} src={pict} alt="featured-items"/>
                                
                        </SwiperSlide>
                    ))}

                </Swiper>
        </Box>
        </Title>
    )
}

const Title = styled.div`
    padding: 3rem 3rem;
`;

const Box = styled.div`
    display: flex;
    flex-direction: row;
    max-height: 30vh;
    width: 100%;
    /* padding: 1rem 3rem; */
    justify-content: space-between;
    
    button{
        width: 8rem;
        font-weight: bold;
        font-size: 1.1.rem;
        height: 5rem;
        align-self: center;
        margin: .5rem;
        background: aliceblue;
        :hover{
            cursor: pointer;
            background: dimgray;
        }
    }
`;

const PictureBox = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    .photo{
        /* border: 2px solid red; */
    }
    img{
        height: 100%;
        width: 100%;
    }
`;

export default FeaturedItems;