import styled from 'styled-components'
import Nav from './Nav'
import BackgroundPhoto from '../img/bannerMedium2.jpg'
import {Link} from 'react-router-dom'

const Banner = () => {
    return(
        <BanDiv>
            <Nav />
                <h1>Custom Furniture Hand Made In The Heart of Queens</h1>

                <button> <Link to='/WhoWeAre'>Learn More</Link></button>
        </BanDiv>
    )
}

const BanDiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    justify-content: space-around;
    justify-content: start;
    width: 100%;
    height: 40vh;
    background-image: url(${BackgroundPhoto});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    button{
        padding: 1rem;
        background-color: #08093c;
        border-radius: .8rem;
        color: beige;
        font-weight: bold;
        width: 10rem;
        margin-top: 3rem;
        a{
            text-decoration: none;
        }
        :hover{
            background-color: #4e5196;
            color: beige;
            cursor: pointer;
            transition: .3s ease-in-out all;
        }
    }
`;

export default Banner;