import styled from 'styled-components'

const CustomItem = ({data}) =>{
    const gallery = []
    for (let i = 1; i < 6; i++){
        gallery.push(data.images[i])
    }
    console.log(data)
    return(
        <Main>
            <TopSection>
                <img src={data.images[0]} alt="highlighted-item"/>

                <div className="blurb"> 
                <h2>{data.title}</h2>
                <p>
                    {data.blurb}
                </p>
                </div>

            </TopSection>

            <BottomSection>
                {gallery.map(pic => (
                    <img src={pic} alt="custom-furniture"/>
                ))}
                

            </BottomSection>
        </Main>
    )
}

const Main = styled.div`
    padding: 3rem 3rem;
    height: 70vh;
    border-bottom: 2px solid gray;
    @media (max-width: 1200px){
        padding: 1rem 1rem;
    }
`;

const TopSection = styled.div`
    display: flex;
    flex-direction: row;
    padding: 1rem 6rem;
    height: 50%;
    justify-content: space-between;
    img{
        width: 40%;
        object-fit: fill;
        height: 100%;
    }
    .blurb{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        width: 55%;
        justify-content: space-evenly;
    }
    h2{
        margin-bottom: 1rem;
    }
    p{
        width: 100%;
    }
    @media (max-width: 1200px){
        padding: 1rem 1rem;
    }
`;

const BottomSection = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 1rem 6rem;
    height: 50%;
    justify-content: space-between;
    img{
        height: 90%;
        width: 18%;
    }
    @media (max-width: 1200px){
        padding: 1rem 1rem;
        img{
            object-fit: cover;
        }
    }

`;

export default CustomItem;