import HomePage from './pages/HomePage'
import GlobalStyles from './components/GlobalStyle'
import HomeCollection from './pages/HomeCollection'
import OfficeCollection from './pages/OfficeCollection'
import CustomFabricationPage from './pages/CustomFabricationPage'
import WhoWeArePage from './pages/WhoWeArePage'
import HowToOrderPage from './pages/HowToOrderPage'
import { Switch, Route, useLocation, HashRouter } from 'react-router-dom'
import {FormspreeProvider} from '@formspree/react'


function App() {
  // const location = useLocation()
  return (
    <HashRouter > 
    <div className="App">
      <GlobalStyles />

      <Switch> 

      <Route exact path="/" > 
      <HomePage />
      </Route>

      <Route exact path="/HomeCollection"> 
      <HomeCollection />
      </Route>

      <Route exact path="/OfficeCollection">
        <OfficeCollection />
      </Route>

      <Route exact path="/CustomFabrication">
        <CustomFabricationPage />
      </Route>

      <Route exact path="/WhoWeAre">
        <WhoWeArePage />
      </Route>

      <FormspreeProvider project="1594843301763088239">
      <Route exact path="/HowToOrder">
        <HowToOrderPage />
      </Route>
      </FormspreeProvider>

      </Switch>
    </div>
    </HashRouter>
  );
}

export default App;
