import styled from 'styled-components'
import CustomItems from './CustomItems'

const CustomFabricationBody = ({ itemData }) => {
    return(
        <Main>
            <IntroDiv>
                <h2>Custom Design and Fabrication</h2>

                <p>At Union Artisan we thrive on imagination and creativity. We are always working hard to come up exciting new design concepts to add to our collection.  However, we realize that there our clients may have needs that extend beyond what we showcase here on our website.  If you have an idea for your space that you would like to see actualized, contact us.  We will work with you to design and fabricate a piece or installment to fit your needs.  Below you'll find a few examples of ideas we've helped our clients create.</p>

            </IntroDiv>
                {itemData.map(data => (
                    <CustomItems data={data} />
                ))}
            {/* <CustomItems itemData={itemData} /> */}

        </Main>
    )
}

const Main = styled.div`
    padding: 3rem 3rem;
    @media (max-width: 1200px){
        padding: 3rem 1rem;
    }
`;

const IntroDiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 30vh;
    justify-content: space-around;
    padding: 2rem 3rem;
    p{
        padding: 1rem 1rem;
    }
`;

export default CustomFabricationBody;

