import styled from 'styled-components'
import furnLogo from '../img/furnitureLogo.jpg'
// link
import {Link} from 'react-router-dom'
import { useState } from 'react'

const Nav = () => {
    const [isShown, setIsShown] = useState(false)
    const [isShown2, setIsShown2] = useState(false)
    return(
        <NavDiv>
            <img src={furnLogo} alt="logo"/>

            <ul>
                <li>
                   <Link to="/"> Home</Link>
                </li>

                <li className="Drop" onMouseEnter={() => setIsShown(true)} onMouseLeave={() => setIsShown(false)}>
                   <Link to="/HomeCollection" > Collections </Link>
                   {isShown && ( 
                       <ul className="down">
                           <li> <Link to='/HomeCollection'>Home</Link></li>
                           <li> <Link to="/OfficeCollection">Office</Link> </li>
                       </ul>
                   )}
                </li>

                <li> <Link to='/CustomFabrication'>Custom Fabrication</Link> </li>

                <li onMouseEnter={() => setIsShown2(true)} onMouseLeave={() => setIsShown2(false)}> <Link to="/WhoWeAre">About</Link>
                {isShown2 &&(
                        <ul className="down">
                            <li> <Link to="/WhoWeAre">Who We Are</Link> </li>
                            <li><Link to="/HowToOrder">How To Order</Link></li>
                        </ul>
                )} 
                </li>
            </ul>
        </NavDiv>
    )
}


const NavDiv = styled.div`
    width: 100%;
    padding: 0rem 3rem;
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: center;
    justify-content: space-between;
    height: 13vh;
    margin-top: 5%;
    .down{
        display: flex;
        flex-direction: column;
        width: 10rem;
        height: auto;
        background-color: #251e1e;
        /* text-align: left; */
        position: absolute; 
        li a{
            font-size: 1rem;
            /* margin: .5rem; */
        }
    }

    img {
        height: 5rem;
        width: 5rem;
        box-shadow: 5px 5px 10px #130e0b;
    }
    ul {
        display: flex;
        flex-direction: row;
        width: auto;
        font-family: 'Crete Round', serif;
        
        a{
            color: beige;
            text-decoration: none;
            font-size: 1.9rem;
            font-weight: bolder;
        }
        li{
            margin: 0rem .5rem;
            list-style-type: none;
            cursor: pointer;
            font-size: 1.2rem;
            font-weight: bold;
            height: 3rem;
            /* text-shadow: 2px 2px black; */
            :hover{
                color: white;
                text-shadow: 2px 2px black;
                transition: .3s ease-in-ease-in-out all;
            }
        }
    }
    @media (max-width: 1000px){
        ul a {
            font-size: 1.3rem;
        }
    }
    @media (max-width: 800px){
    }
`;

export default Nav;