import { createGlobalStyle } from 'styled-components'

// color scheme
// black #130e0b 
// purple #77588e 
// white #efefdc 
// red #c10916 
// blue #08093c 


const GlobalStyle = createGlobalStyle`
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    p {
        color: #333232;
        font-size: 1.5rem;
        letter-spacing: .1rem;
        font-family: 'Montserrat', sans-serif;
    }
    h1{
        color: beige;
        font-size: 3rem;
    }
    @media (max-width: 1300px){
        h1{
            font-size: 2rem;
        }
        p{
            font-size: 1rem;
        }
    }
    @media (max-width: 800px){
        h1{
            font-size: 1.5rem;
        }
    }

`;

export default GlobalStyle;