import FurnitureItem from './FurnitureItem'
import styled from 'styled-components'
import { useState } from 'react'


const CollectionBody = ({furnData}) =>{
    const [list, setList] = useState(furnData)


    return(
        <Main>
            <NavSection>
                <h2>Home Collection</h2>
                <h3 onClick={() => setList(furnData)}>ALL</h3>

                <ul>
                    <li onClick={() => setList(furnData.filter(data => data.category === "coat-hanger"))}>COAT HANGERS</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "coffee-table"))}>COFFEE TABLES</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "console"))}>CONSOLES</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "dining-table"))}>DINING TABLES</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "end-table"))}>END TABLES</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "garden"))}>GARDEN</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "headboard"))}>HEADBOARDS</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "hearth"))}>HEARTH</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "kitchen"))}>KITCHEN</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "lighting"))}>LIGHTING</li>
                    <li onClick={() => setList(furnData.filter(data => data.category === "shelving"))}>SHELVING</li>
                </ul>
            </NavSection>

           
            
           
           <>
           {list && ( 
               <FurnPortfolio>
                {list.map(furn =>
                    <FurnitureItem data={furn} key={furn.id}/> 
                )}
                </FurnPortfolio>
            )}
            </>
            
               
        </Main>
    )
};

const Main = styled.div`
    display: flex;
    flex-direction: row;
    padding: 3rem 13rem;
    justify-content: center;
    align-items: start;
    @media (max-width: 1400px){
        flex-direction: column;
        padding: 3rem 3rem;
    }

`;

const NavSection = styled.div`
    display: flex;
    flex-direction: column;
    width: 25%;
    justify-content: flex-end;
    height: 30rem;
    h2{
        color: #c10916;
        
    }
    h3{
        margin-top: 1rem;
        :hover{
            cursor: pointer;
            color: green;
        }
    }
    ul{
        margin-top: 2rem;
        list-style: none;
        li{
            margin: .5rem;
            color: #333232;
            :hover{
                cursor: pointer;
                color: green;
                transition: ease-in-out;
                font-weight: bold;
            }
        }
    }
    @media (max-width: 1400px){
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: flex-start;
        height: auto;
        ul{
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
        }
    }
`;

const FurnPortfolio = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 50vh;
    min-width: 75%;
    justify-content: space-evenly;
    @media (max-width: 1200px){
        grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 1400px){
        min-width: 100%;
    }
`;

export default CollectionBody;