import styled from 'styled-components'
import {Link} from 'react-router-dom'

const WhoWeAreBody = ({data}) =>{
    console.log(data[0].paragraphs)
    const images = data[0].images
    const paragraphs = data[0].paragraphs
    return(
        <MainDiv>
            <NavDiv>
                <h2>About Gloria's</h2>
                <p><Link to="/WhoWeAre">WHO WE ARE</Link></p>
                <p> <Link to="HowToOrder">HOW TO ORDER</Link> </p>
            </NavDiv>

            <BodyDiv>
                <p>{paragraphs[0]}</p>

                <div className="first">
                <img src={images[0]} alt=""/>
                <p>{paragraphs[1]}</p>
                </div>

                <div className="second">
                    <img src={images[1]} alt=""/>
                    <p>{paragraphs[2]}</p>
                </div>

                <img className="bottom-photo" src={images[2]} alt=""/>

            </BodyDiv>
        </MainDiv>
    )
}

const MainDiv = styled.div`
    padding: 3rem 15rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media (max-width: 1200px){
        padding: 3rem 3rem;
    }
`;

const NavDiv = styled.div`
    display: flex;
    flex-direction: column;
    height: auto;
    width: 25%;
    justify-content: flex-start;
    align-items: flex-start;
    p{
        margin: .5rem 0rem;
        a{
            color: #333232;
            text-decoration: none;
            :hover{
                cursor: pointer;
                color: green;
                transition: ease-in-out;
                font-weight: bold;
            }
        }
    }
`;

const BodyDiv = styled.div`
    display: flex;
    flex-direction: column;
    width: 75%;
    height: auto;
    justify-content: space-between;
    .first{
        text-align: center;
        margin: 1rem 0rem;
        img{
            float: left;
            margin-right: .5rem;
            height: 20rem;
            width: 20rem;
            border: solid;
        }
        p{
            text-align: justify;
            /* font-size: 2rem; */
        }
    }
    .second{
        text-align: center;
        margin: 1rem 0rem;
        img{
            float: right;
            margin-left: .5rem;
            height: 15rem;
            width: 15rem;
            border: solid;
        }
        p{
            text-align: justify;
            /* font-size: 2rem; */
        }
    }
    .bottom-photo{
        height: 30rem;
        width: 100%;
        object-fit: fill;
    }
`;

export default WhoWeAreBody;