import styled from 'styled-components'
import Nav from './Nav'
import BackgroundPhoto from '../img/bannerMedium2.jpg'

const OfficeCollectionBanner = () => {
    return(
        <BanDiv>
            <Nav />
                <h1>Hand Made Collections</h1>

        </BanDiv>
    )
}

const BanDiv = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    justify-content: space-around;
    justify-content: start;
    width: 100%;
    height: 40vh;
    background-image: url(${BackgroundPhoto});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    button{
        padding: 1rem;
        background-color: #08093c;
        border-radius: .8rem;
        color: beige;
        font-weight: bold;
        width: 10rem;
        :hover{
            background-color: #24253b;
            color: beige;
            cursor: pointer;
            transition: .3s ease-in-out all;
        }
    }
`;

export default OfficeCollectionBanner;