import styled from 'styled-components'

const MissionStatement = () => {
    return(
        <MissionDiv>
            <h2>We believe the past is worth holding onto.</h2>

            <p>
            <span>G</span>loria's is a small NYC based company born from the neighborhoods of Flushings. Our mission is handcrafting custom furnishings. Our products are designed to showcase the character, and history that runs through the community that we feel so fortunate to be a part of. 
            </p>
            <p>
            <span>O</span>ur master carpenter Ali brings a rich tradition of wood making from decades of experience.
            </p>
        </MissionDiv>
    )
}

const MissionDiv = styled.div`
    display: flex;
    flex-direction: column;
    padding: 1rem 3rem;
    align-items: center;
    justify-content: center;
    justify-content: space-around;
    min-height: 30vh;
    p{
        width: 50%;
    }
    h2{
        font-family: "Satisfy", cursive;
        font-size: 3rem;
    }
    span{
        font-size: 1.9rem;
        color: black;
        font-weight: bold;
        font-family: 'Pacifico', cursive;
        font-style: italic;
        padding: 0rem .3rem;
    }
    @media (max-width: 1000px){
        h2{
            font-size: 2rem;

        }
        p{
            width: 80%;
        }
    }
`;

export default MissionStatement;