import OfficeCollectionBanner from '../components/OfficeCollectionBanner'
import Footer from '../components/Footer'
import OfficeCollectionBody from '../components/OfficeCollectionBody'
import { OfficeFurnCollection } from '../Data'
import {useState} from 'react'


const OfficeCollection = () => {
    const [officeFurnData, setOfficeFurnData] = useState(OfficeFurnCollection)
    return(
        <div>
            <OfficeCollectionBanner />
            <OfficeCollectionBody officeFurnData={officeFurnData} setOfficeFurnData={setOfficeFurnData}/>
            <Footer />
        </div>
    )
}

export default OfficeCollection;