import PlaceHolder from '../img/carpenter3.jpg'
import styled from 'styled-components'

const FurnitureItem = ({data}) => {
    return(
        <Main>
            <img src={data.img} alt=""/>

            <h2>{data.name}</h2>
        </Main>
    )
}

const Main = styled.div`
    display: flex;
    flex-direction: column;
    margin: 1rem;
    img {
        height: 10rem;
        width: auto;
        object-fit: cover;
        
    }
    h2{
        color: rgba(26,26,26,.7);
        align-self: center;
        margin: .2rem;
        font-size: 1.5rem;
    }
    @media (max-width: 1400px){
        h2{
            font-size: 1rem;
        }
    }
`;

export default FurnitureItem;