import styled from 'styled-components'
import nycMap from '../img/nycMap.JPG'
import MessageSent from './MessageSent'
import { useForm, ValidationError } from '@formspree/react'
import {Link} from 'react-router-dom'

const WhoWeAreBody = () =>{
    const [state, handleSubmit] = useForm("contactMe")
    if(state.succeeded){
        return <MessageSent />
    }
    return(
        <MainDiv>
            <NavDiv>
                <h2>About Gloria's</h2>
                <p><Link to="/WhoWeAre">WHO WE ARE</Link></p>
                <p> <Link to="HowToOrder">HOW TO ORDER</Link> </p>
            </NavDiv>

            <BodyDiv>
                <p>If you like what you see, or are interested in a custom website or commissioning a custom project, please contact me by email so I can provide you with more information:</p>

                <div className="contact">
                    <span><p>info@email.com</p></span> 
                    <p>555-555-5555</p>
                </div>

                <ContactFormSection>
                            <Form onSubmit={handleSubmit}>
                                <FormDiv>
                                    <label htmlFor='Name'>Name *</label>
                                    <input type="text" name='name' id='name' />
                                    <ValidationError field="first-name" prefix="first-name" errors={state.errors} />
                                </FormDiv>

                                <FormDiv>
                                    <label htmlFor="email">Last Name *</label>
                                    <input type="text" name='last-name' id='last-name' />
                                    <ValidationError field="last-name" prefix="last-name" errors={state.errors} />
                                </FormDiv>
                                
                                <FormDiv2 >
                                    <label htmlFor="email">Email Address *</label>
                                    <input type="text" name='email' id='email' />
                                    <ValidationError field="email" prefix="email" errors={state.errors} />
                                </FormDiv2>

                                <FormDiv2>
                                    <label htmlFor="email">Subject *</label>
                                    <input type="text" name='subject' id='subject' />
                                    <ValidationError field="subject" prefix="subject" errors={state.errors} />
                                </FormDiv2>

                                <FormDiv2>
                                    <label htmlFor="email">Message *</label>
                                    <textarea  type="text" name='message' id='message' />
                                    <ValidationError field="message" prefix="message" errors={state.errors} />
                                </FormDiv2>

                                {/* <Message>
                                     <label htmlFor="message"></label> 
                                     <textarea type="text" name='message' id='message' placeholder="message" /> 
                                 </Message> */}

                                <ButtonDiv>
                                    <input type="submit" value="Send Message" />
                                    <ValidationError errors={state.errors} />
                                </ButtonDiv>

                            </Form>
                        </ContactFormSection>
                            
                        <NYCMap>
                            <img src={nycMap} alt=""/>
                            <p>Please note that because our products and commissioned designs tend to be both large and heavy by nature, we are focused on primarily serving clients in the NYC area.  For those without the means to pick up and transport items from our location in the city, delivery within one and a half hours of Queens can be arranged for an additional fee.</p>
                        </NYCMap>


            </BodyDiv>
        </MainDiv>
    )
}

const NYCMap = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    img{
        width: 100%;
    }
    p{
        padding: 2rem;
    }
`;

const MainDiv = styled.div`
    padding: 3rem 15rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    @media (max-width: 1200px){
        padding: 3rem 3rem;
    }

`;

const NavDiv = styled.div`
    display: flex;
    flex-direction: column;
    height: auto;
    width: 25%;
    justify-content: flex-start;
    align-items: flex-start;
    p{
        margin: .5rem 0rem;
        a{
            color: #333232;
            text-decoration: none;
            :hover{
                cursor: pointer;
                color: green;
                transition: ease-in-out;
                font-weight: bold;
            }
        }
        
    }
`;

const BodyDiv = styled.div`
    display: flex;
    flex-direction: column;
    width: 75%;
    height: auto;
    justify-content: space-between;
    
    .contact{
        display: flex;
        width: 100%;
        justify-content: space-between;
        margin-top: 1rem;
        span p{
            color: green;
        }
    }
    .first{
        text-align: center;
        margin: 1rem 0rem;
        img{
            float: left;
            margin-right: .5rem;
            height: 20rem;
            width: 20rem;
            border: solid;
        }
        p{
            text-align: justify;
            
            /* font-size: 2rem; */
        }
    }
    .second{
        text-align: center;
        margin: 1rem 0rem;
        img{
            float: right;
            margin-left: .5rem;
            height: 15rem;
            width: 15rem;
            border: solid;
        }
        p{
            text-align: justify;
            /* font-size: 2rem; */
        }
    }
    .bottom-photo{
        height: 30rem;
        width: 100%;
        object-fit: fill;
    }
    p{
        text-align: center;
    }
    @media (max-width: 1200px){
        text-align: center;
    }
`;

const ContactFormSection = styled.div`
    display: flex;
    flex-direction: column;
    width: 80%;
    align-self: center;
    height: 50vh;
    padding: 2rem 0rem;
    @media (max-width: 1300px){
        width: 80%;
        height: 100%;
    }
`;

const Form = styled.form`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-between;
    @media (max-width: 1300px){
        height: 100%;
    }
`;

const FormDiv = styled.div`
    width: 45%;
    margin: .3rem;
    border-radius: .5rem;
    input{
        width: 100%;
        height: 2.5rem;
        padding: .2rem;
        font-size: 1.2rem;
        /* border-radius: .3rem; */
        border: 3px solid #a3a1a1;
    }
`;

const FormDiv2 = styled.div`
    width: 100%;
    margin: .3rem;
    border-radius: .5rem;
    display: flex;
    flex-direction: column;
    input{
        width: 100%;
        height: 2.5rem;
        padding: .2rem;
        font-size: 1.2rem;
        /* border-radius: .3rem; */
        border: 3px solid #a3a1a1;
    }
    textarea[id='message']{
        height: 10rem;
    }
`;

const ButtonDiv = styled.button`
    width: 10rem;
    height: 5rem;
    margin: .3rem;
    border-radius: .5rem;
    input{
        width: 100%;
        height: 100%;
        padding: .2rem;
        font-size: 1rem;
        border-radius: .3rem;
        /* border: 3px solid black; */
        color: #f2eaea;
        background: #262424;
        :hover{
            background: #746d6d;
            color: #262424;
            cursor: pointer;
        }
}    
`;


export default WhoWeAreBody;