import CustomFrabricationBanner from '../components/CustomFabricationBanner'
import CustomFabricationBody from '../components/CustomFabricationBody'
import Footer from '../components/Footer'
import {CustomItemData} from '../Data'
import {useState} from 'react'

const CustomFabricationPage = () =>{
    const [itemData, setItemData] = useState(CustomItemData)
    return(
        <div>
        <CustomFrabricationBanner />
        <CustomFabricationBody itemData={itemData} setItemData={setItemData} />
        <Footer />
        </div>
    )
}

export default CustomFabricationPage;