import WhoWeAreBanner from '../components/WhoWeAreBanner'
import WhoWeAreBody from '../components/WhoWeAreBody'
import Footer from '../components/Footer'
import {WhoWeAreData} from '../Data'
import {useState} from 'react'

const WhoWeArePage = () => {
    const [whoData, setWhoData] = useState(WhoWeAreData)
    return(
        <div>
            <WhoWeAreBanner />
            <WhoWeAreBody data={whoData} setWhoData={setWhoData} />
            <Footer />
        </div>
    )
}

export default WhoWeArePage;