import styled from 'styled-components'
import carpenter1 from '../img/carpenter1.jpg'
import carpenter2 from '../img/carpenter3.jpg'

const AboutSection = () => {
    return(
        <AboutDiv className="home">
            <div className="box box1">
                <img src={carpenter1} alt="carpenter1"/>
                <div className="para"> 
                    <h2>About Gloria's</h2>
                    <p>
                        Find out about Union Artisan and our dedication to crafting quality art and furniture with materials gathered from the neighborhoods of Capitol Hill.
                    </p>
                    <p style={{ fontFamily: 'Montserrat, sans-serif', color: '#3d9991'}}>Learn More --&gt;</p>
                </div>
            </div>

            <div className="box box2">
            <img src={carpenter2} alt="carpenter2"/>
                    <div className="para">
                        <h2>Our Dedication To You</h2>
                        <p>
                        Whether it's your garden, office or home, Union Artisan has something to enliven your environment.  Take a look through our unique collections.
                        </p>
                        <p style={{ fontFamily: 'Montserrat, sans-serif', color: '#3d9991'}}>Check out our Home Collection --&gt;</p>
                    </div>
            </div>
        </AboutDiv>
    )
}


const AboutDiv = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 6rem;
    width: 100%;
    height: 50vh;
    .box{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        img{
            max-height: 50%;
            width: 50%;
            object-fit: contain;
        }
        .para{
            height: auto;
            width: 80%;
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: space-around;
            h2{
                font-family: 'Montserrat', sans-serif;
                color: #3d9991;
            }
            p{
                color: black;
                margin: .5rem 0rem;
            }
        }
    }
    @media (max-width: 1000px){
        padding: 3rem 3rem;
        height: auto;
        flex-direction: column;
    }
`;


export default AboutSection;